				<font face="宋体" style="font-weight:bold;">“更轻松的安全交流”</font>
				<font face="宋体">
					为目标，让每个微信用户都拥有自己的专属房间为核心，以微信朋友分层管理为基础。微信用户进入空间无需下载，不留存任何个人身份信息，个性化的虚拟房间作为身份，完全以虚拟房间进行交流、展示、表达，提供一键删除双方手机聊天记录、三种情形三种密码设置等功能，可以满足熟人交往和陌生人交流的安全需求。
				</font>
			</span>
		</p>
